FlexboxItem,
Divider,
XInput,
Group,
XButton,
Cell,
XAddress,
XHeader,
Loading,
Confirm
},
data () {
return {
name: '',   //收货人
tel: '',   //手机号码
area: [],   //所在区域
detailaddress: '',  //详细地址
address_id: '',  //地址id
disabled: true,
swit: false,
choosedAddress: '',
defaultAddress: '选择区域',
nameValue: function(value){
return {
valid:  /^.{1,20}$/.test(value),
msg: "验证码有误!"
}
},
telValue: function (value) {
return {
valid: value.length >= 11 && /^1[34578]\d{9}$/.test(value),
msg: "手机号输入有误, 请输入11位手机号码"
}
},
areaValue: function (value) {
return {
valid: value.length === 0,
msg: "验证码有误!"
}
},
detailaddressValue: function (value) {
return {
valid: /^.{1,50}$/.test(value.trim()),
msg: "验证码有误!"
}
},
addressData: districts,
title: '所在区域',
infoList: {},
address_One:0,
loadingShow:false,
member_id: '',
showTip: '', //提示语
showtishi: false,//控制弹窗显示隐藏
showCancel: false,
hasChoosed: false
}
},
computed: {
...mapState({
addressList: state => state.user.addressList
})
},
created () {
let index = this.$route.query.infoList
console.log(index, this.addressList)
let addressEdit = this.addressList[index]
const {name, phone, address, district_path, id} = addressEdit
this.name = name
this.tel = phone
this.detailaddress = address.split(district_path)[1]
this.defaultAddress = district_path
this.address_id = id
this.area = [addressEdit.district_id]
},
methods: {
...mapActions({
getAddress: 'getAddressList',
createAddress: 'createNewAddress',
updateAddress: 'updateAddress',
deleteAddress: 'deleteAddress'
}),
chooseArea (type) {
if (type) {
this.hasChoosed = true
}
console.log(arguments)
},

checkName (name) {
if (name == '') {
console.log('名称不能为空')
}
},

async saveInfo () {
console.log(this.swit, this.area)
if (this.swit) {
this.loadingShow = true;




























console.log(this.detailaddress)
const option = {
id: this.address_id,
phone: this.tel,
name: this.name,
districtId: this.area[2] ? this.area[2] : (this.area[1] ? this.area[1] : this.area[0]),
address: (this.hasChoosed ? this.choosedAddress : this.defaultAddress) + this.detailaddress
}
console.log(option)
let res = await this.updateAddress(option)
this.loadingShow = false;
this.$router.go(-1)
}
console.log(this.name + this.tel + this.area + this.detailaddress, '我们的选择')
},

validRule () {
if (this.$refs.refname.valid == true && this.name != '' && this.$refs.reftel.valid == true && this.tel != '' && this.$refs.refdetailaddress.valid == true && this.detailaddress != '') {
